import React, { Suspense } from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";

const randonParcel = React.lazy(() => import("../parcels/default"));
const randonParcela = React.lazy(() => import("../parcels/default"));
const randonParcelb = React.lazy(() => import("../parcels/default"));

export default function Routes() {
  var token = localStorage.getItem("token");

  if (!(token && token === "testando")) {
    history.push("/auth/signin");
  }

  return (
    <Suspense fallback={<h1>Carregando...</h1>}>
      <BrowserRouter basename="app">
        <Switch>
          <Route exact path="/vue-todo-list" component={randonParcel} />
          <Route exact path="/parcel-angular" component={randonParcela} />
          {location.hostname === 'localhost' && <Route exact path="/development" component={randonParcelb} /> }          
        </Switch>
      </BrowserRouter>
    </Suspense>
  );
}
